.label-name{
  font-size: 1.4rem;
  font-weight: 500;
  fill: rgb(255, 255, 255);
  text-transform: capitalize;
  text-shadow: rgba(0, 0, 0, 0.5) -1px 1px 1px;

}
.label-value{
  font-size: 1.4rem;
  font-weight: 500;
  fill: rgb(255, 255, 255);
  text-transform: capitalize;
  text-shadow: rgba(0, 0, 0, 0.5) -1px 1px 1px;
}
.difference-text{
  font-size: 1.4rem;
  font-weight: 500;
  fill: #2BD784;
  text-transform: capitalize;
  text-shadow: rgba(0, 0, 0, 0.5) -1px 1px 1px;
}
.difference-line-to-bar{
  stroke: white;
  stroke-opacity: 0.5;

}
.endline-bar{
  stroke: white;
}
.svg{
  overflow: unset;
}
.svg-container{
  padding-bottom: 50px;
}